import React,{Component} from 'react';
import axios from 'axios';
import  '../style/MovieApp.scss';

class MovieApp extends Component {
    state = {
        data:[],
    }
    componentDidMount() {
        const BASE_URL = 'https://api.themoviedb.org/3';
        const KEY = 'api_key=3fd2be6f0c70a2a598f084ddfb75487c&page=1'
        const MovieAPI = BASE_URL+'/discover/movie?with_genres=35&primary_release_year=2020&'+KEY;
        axios
            .get(MovieAPI)
            .then(response => {
                this.setState({data: response.data.results});
            })
            .catch(err => console.error(err));
    }
    handleAPI = () => {
        const data = this.state.data;
        const IMG_PATH = 'https://image.tmdb.org/t/p/w1280';
        // console.log(data);
        const list = data.map((movie) => {
            let color;
            const rate = movie.vote_average;
            const time = movie.release_date;
            if(rate>= 8){
              color = '#90e886';
            }
            else if(rate >=5 && rate < 8){
              color = '#ffa502';
            }
            else{
              color = '#ef0b1d';
            }
            const color_style = {
              color: color,
            }
            const time_color = {
              color:'#fd4b13',
            }
            return (
            <div className="movie-card" style={this.state.style}>
            <div className="movie-img">
                <img src={IMG_PATH+movie.poster_path} alt='image'/>
            </div>
            <div className="movie-des">
            <div className="movie-inf">
            <h3>{movie.original_title}</h3>
            <h6 style={time_color}>{time.split('-').reverse().join('-')}</h6>
            </div>
            <div className="movie-rate" style={color_style}>{rate}</div>
            </div>
            <div className="overview" style={this.state.style}>
              <h2>Overview</h2>
              <p>{movie.overview}</p>
            </div>
            </div>
        )});
        return list;
    }
    render() {
        return (
            <div className='movie-app'>
            <div className='container'>
            <h1>Movie</h1>
            {this.handleAPI()}
            </div>
            </div>
        )
    }
}
export default MovieApp;
