import React,{Component} from 'react';
import {BrowserRouter as Router,Route,Switch,NavLink,Redirect} from 'react-router-dom';
import MovieApp from './components/MovieApp';
import Latest from './components/Latest';
import TV_Shows from './components/TV_Shows';
import Search from './components/Search';
import logo from './assets/img/logo.png';
import bg from './assets/img/bg.jpg';
import name from './assets/img/name.png';
import play from './assets/img/play.png';
import trailer from './assets/mp4/trailer.mp4';
import close from './assets/img/close.png';
import notFound from './assets/img/notFound.jpg';
import './style/App.scss';

const Navbar = ({callback}) => {
    
    return (
        <header>
            <div className="logo"><img src={logo} alt="logo" /></div>
            <ul className="nav">
                <li>
                    <NavLink to = '/'>Home</NavLink> 
                </li>
                <li>
                    <NavLink to = '/tv-shows'>TV Shows</NavLink> 
                </li>
                <li>
                    <NavLink to = '/movie'>Movie</NavLink> 
                </li>
                <li>
                    <NavLink to = '/latest'>Latest</NavLink>  
                </li>
                <li>
                    <NavLink to = '/list'>My List</NavLink> 
                </li>
            </ul>
            <div className="search">
            <input type="text" className="" placeholder="Search" onKeyDown={callback} />
            <i className="fas fa-search"></i>
            </div>
        </header>
    )
}
class Home extends Component {
    video = React.createRef();
    state = {
        className:'trailer',
    }
    handlePlayTrailer(){
        // console.log(this.video.current);
        this.video.current.currentTime = 0;
        this.video.current.pause();
        const newClassName = this.state.className === 'trailer' ? 'trailer active' : 'trailer';
        this.setState({className: newClassName});
    }
    render() {
        return (
            <>
            <div className="banner">
                <img src={bg} className="bg" alt="background"/>
                <div className="content">
                <img src={name} className="movieTitle" alt="title"/>
                <h4>
                    <span>2020</span>
                    <span><i>12+</i></span>
                    <span>1h 55min</span>
                    <span>Action</span>
                </h4>
                <p>Mulan (2020) thuộc xu hướng làm lại loạt tác phẩm hoạt hình thành công thuộc thập niên 1990 của Disney. Nội dung dựa trên điển tích của Trung Quốc, về cô gái giả trai đi lính thay cha già yếu và trở thành nữ anh hùng dân tộc. Một câu chuyện đậm đà bản sắc phương Đông với sự góp mặt của hàng loạt sao lớn Trung Hoa. Cùng gặp cô công chúa Disney tiếp theo trên màn ảnh rộng - Hoa Mộc Lan</p>
                <div className="buttons">
                <NavLink to='/'><i className="fas fa-play"></i> Play</NavLink>
                <NavLink to='/'><i className="fas fa-plus"></i> My List</NavLink>
                </div>
                </div>
                <NavLink to='/' className='play' onClick={this.handlePlayTrailer.bind(this)}><img src={play} alt='Play'/>Watch Trailer</NavLink>
            </div>
            <div className={this.state.className}>
            <video src={trailer} controls={true} ref ={this.video} ></video>
            <img src={close} className='close'  onClick={this.handlePlayTrailer.bind(this)} alt='Close'/>
            </div>
            </>
        )
    }
}

//pages
const TVShowsPage = (props) => <TV_Shows/>; 
const HomePage = (props) => <Home/>; 
const MoviePage = (props) => <MovieApp/>;
const LatestPage = (props) => <Latest/>;
// const SearchPage = ({key}) => <Search keywords={key}/>;
const NotFound = () => <div className='not-found'><img src={notFound} alt='notfound'/></div>;

class App extends Component{
    state = {
        key:'',
        isSearch:false,
    }
    handleSearch = (e) =>{
        // console.log(e.key);
        if(e.key === 'Enter'){
            // console.log(this.props);
            const newKey = e.target.value;
            console.log(newKey);
            this.setState({key: newKey});
            this.setState({isSearch: true});
            e.target.value = ''; 
        }
    }
    render() {
        return (
            <Router>
                <Navbar callback={this.handleSearch}/>
                <div className="app">
                    <Switch>
                    <Route path='/tv-shows' component={(props) => {
                        if(this.state.isSearch){
                            this.setState({isSearch:false});
                            return <Redirect to ='/search'/>
                        }
                        else{
                            return <TVShowsPage {...props}/>
                        }
                    }} />
                    <Route path='/movie' component={(props) => {
                        if(this.state.isSearch){
                            this.setState({isSearch:false});
                            return <Redirect to ='/search'/>
                        }
                        else{
                            return <MoviePage {...props}/>
                        }
                    }} />
                    <Route path='/latest' component={(props) => {
                        if(this.state.isSearch){
                            this.setState({isSearch:false});
                            return <Redirect to ='/search'/>
                        }
                        else{
                            return <LatestPage {...props}/>
                        }
                    }} />
                    <Route path='/search' component={(props) => <Search {...props} keywords={this.state.key}/>} />
                    <Route exact path='/' component={(props) => {
                        if(this.state.isSearch){
                            this.setState({isSearch:false});
                            return <Redirect to ='/search'/>
                        }
                        else{
                            return <HomePage {...props}/>
                        }
                    } }/>
                    <Route component={NotFound} />
                    </Switch>
                </div>
            </Router>
        )
    }
}
export default App;